@import '../../styles/variables.scss';

#myModal {
    width: clamp(325px, 75%, 1400px);
    container-type: inline-size;
    padding: 2rem 2rem 0 0;
    border-radius: 0.5rem;

    &::backdrop {
        background: linear-gradient(45deg, rgba(0, 0, 255, 0.75) 0%, rgba(173, 216, 230, 0.75) 50%, rgba(255, 99, 71, 0.75) 100%);
    }

    button {
        min-width: 10rem;
    }
}
